<template>
  <div id="app">
    <b-container class="mt-4">
      <b-row>
        <b-col lg="6">

          <label for="textarea1"><h3>Введите шифр</h3></label>

          <b-form-textarea
            id="textarea1"
            size="lg"
            rows="4"
            placeholder="Текст сюды"
            v-model="text"
          ></b-form-textarea>

      </b-col>
      <b-col lg="6">
          
          <label for="textarea2"><h3>Результат</h3></label>

          <b-form-textarea
            id="textarea2"
            size="lg"
            rows="4"
            disabled
            v-model="textCipher"
          ></b-form-textarea>

      </b-col>
      </b-row>

      <b-row class="mt-4">
        <b-col lg="2" class="mt-2">
          <b-form-select v-model="selected" :options="options"></b-form-select>
          <div class="mt-3">Ключ: {{ selected }}</div>
        </b-col>

        <b-col lg="2" class="mt-2">
          <b-button @click="decrypt" variant="primary">Расшифровать</b-button>
        </b-col>
        <b-col lg="2" class="mt-2">
          <b-button @click="encrypt" variant="danger">Зашифровать</b-button>
        </b-col>

      </b-row>
    </b-container>
  </div>
</template>

<script>

import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

export default {
  name: 'App',
  data() {
    return {
      selected: null,
      text: '',
      textCipher: '',
      alphabet: 'АБВГДЕЁЖЗИЙКЛМНОПРСТУФХЦЧШЩЪЫЬЭЮЯабвгдеёжзийклмнопрстуфхцчшщъыьэюя',
      alphabetCipher: ''
    }
  },
  computed: {
    options() {
      let options = [ { value: null, text: 'Выбрать ключ' } ];

      for (let i = 1; i <= 33; i++) {
        options.push({ value: i, text: i})
      }

      return options
    }
  }, 
  methods: {
    encrypt() {
      this.alphabetCipher = this.alphabet;
      this.alphabetCipher = this.shiftArray(this.alphabetCipher.split(''), this.selected).join('');

      this.textCipher = this.cryptText(this.text, this.alphabet, this.alphabetCipher);
    },
    decrypt() {
      this.alphabetCipher = this.alphabet;
      this.alphabetCipher = this.shiftArray(this.alphabetCipher.split(''), this.selected).join('');

      this.textCipher = this.cryptText(this.text, this.alphabetCipher, this.alphabet);
    },
    shiftArray(arr, num) {
      return arr.slice(num).concat( arr.slice(0, num))
    },

    cryptText(text, alphabet, alphabetCipher) {
      const numArr = [];
      const textArr = text.split('');

      textArr.forEach(el => {
        if ( alphabet.indexOf(el) !== -1 ) {
          numArr.push(alphabet.indexOf(el))
        } else {
          numArr.push(el)
        }
      })

      return numArr.map(n => {
        let arr = [];

        if (Number.isInteger(n)) {
          arr = alphabetCipher[n]
        } else {
          arr = n
        }

        return arr
      }).join('')
    }
  }
}
</script>

<style>
body {
  background-color: #373636;
  color: white;
  font-family: 'Roboto', sans-serif;
}
</style>
